<template>
	<view>
		<view class="search-box">
			<uni-search-bar @input="input" @confirm="onConfirm" :radius="100" cancelButton="none"></uni-search-bar>
		</view> 
		<view class="sugg-list" v-if="searchResults.length !== 0">
			<view class="sugg-item" v-for="(item,i) in searchResults" :key="i" @click="gotoSuchTalk(item.id)">
				<view class="goods-name">{{item.content}}</view>
				<uni-icons type="arrowright" size="16"></uni-icons>
			</view> 
		</view>
		<view class="history-box" v-else>
			<view class="history-title"> 
				<text>搜索历史</text>
				<uni-icons type="trash" size="17" @click="clean"></uni-icons>
			</view>
			<view class="history-list">
				<uni-tag class="tag" :text="item" v-for="(item, i) in histories" :key="i" @click="tagSwitch(item)"></uni-tag>
			</view> 
		</view>
	</view>
</template> 
 
<script> 
	import { $http } from '@escook/request-miniprogram'
	uni.$http = $http
	$http.baseUrl = 'https://database.ccjy16.top'
	
	//请求拦截器
	$http.beforeRequest = function (options) {
	  uni.showLoading({
	  	title: '数据加载中...',
	  }) 
	} 
	//响应拦截器
	$http.afterRequest = function () {
	  uni.hideLoading()
	}
	export default {
		data() {
			return {
				timer: null,
				kw: '',
				"searchResults": [],
				"historyList": []
			};
		}, 
		onLoad() {
				this.historyList = JSON.parse(uni.getStorageSync("kw") || "[]")  
		},
		methods:{
			input(e){
				this.kw = e
				clearTimeout(this.timer)
				this.timer = setTimeout(() => {
					this.getSearchList(e)
				}, 500)
			},
			onConfirm(){
				if(this.kw.length === 0){
					return
				}
				uni.navigateTo({
				  url: '/subpkg/searchList/searchList?data=' + JSON.stringify(this.kw)
				});
			},
			async getSearchList(e){
				if(e.length === 0){
					this.searchResults = []
					return
				}
				const res = await $http.get("/chatSearchServlet?index=" + e)
				if(res.statusCode !== 200){
					return uni.showToast({
						title:'数据请求失败！',
						duration: 1500,
						icon: 'none'
					})
				}
				this.searchResults = res.data
				this.saveSearchHistory(e)
			},
			saveSearchHistory(e){
				this.historyList.push(e)
				const set = new Set(this.historyList)
				set.delete(e)
				set.add(e)
				this.historyList = Array.from(set)
				uni.setStorageSync("kw", JSON.stringify(this.historyList))
			},
			gotoDetail(t){
				uni.navigateTo({
					url: "/pages_shop/goods_detail/goods_detail?id=" + t
				})
			},
			clean(){
				this.historyList = []
				uni.setStorageSync("kw", "[]") 
			},
			 tagSwitch(item){
				 uni.navigateTo({
				   url: '/subpkg/searchList/searchList?data=' + JSON.stringify(item)
				 });
			 },
			 gotoSuchTalk(id){
			 	uni.navigateTo({
			 		url: '/subpkg/suchtalk/suchtalk?id=' + id
			 	})
			 },
		},
		computed:{ 
			histories(){
				return [...this.historyList].reverse()
			}
		}
	}
</script>

<style lang="scss">
	.search-box{
		position: sticky;
		top: 0;
		z-index: 999;
	}
	.sugg-list{
		padding: 0 5px;
		
		.sugg-item{
			font-size: 12px;
			padding: 13px 0;
			border-bottom: 1px solid #efefef;
			display: flex;
			align-items: center;
			justify-content: space-between;
			
			.goods-name{
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				margin-right: 3px;
			}
		}
	} 
	.history-box{
		padding: 0 5px;
		.history-title{ 
			display: flex; 
			justify-content: space-between;  
			height: 40px;
			align-items: center;
			font-size: 13px;
			border: 1px solid #efefef;
		}
		.history-list{
			display: flex;
			flex-wrap: wrap;
			.tag{
				margin-top: 5px;
				margin-right: 5px;  
			}
		}
	} 
</style>
